import { Component, OnInit } from '@angular/core';
import { DataService } from '../data.service';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { FormsModule} from"@angular/forms";
import { ContactService } from '../contact/contact.service';
import { NgForm } from '@angular/forms'
import { from } from 'rxjs';
import * as $ from 'jquery';

@Component({
  selector: 'app-contact',
  templateUrl: './contact.component.html',
  styleUrls: ['./contact.component.scss']
})
export class ContactComponent implements OnInit {
  registerForm: FormGroup;
  submitted = false;
  loading = false;
  
constructor(private data: DataService, private contactservice: ContactService, private formBuilder: FormBuilder) { }
 shoeSuccessMeassage: boolean;
 
  ngOnInit() {
    this.registerForm= this.formBuilder.group({
      fullname: ['', Validators.required],
      email:['', [Validators.required, Validators.email]],
      mobile:['',Validators.required],
      location:['', Validators.required]
    });
    $("button").click(function(){
      $("#div1").fadeIn();
      $("#div2").fadeIn("slow");
      $("#div3").fadeIn(3000);
  });

    
  
  }  
  
    get f(){
      return this.registerForm.controls;
    }
    onSubmit(){
      this.submitted= true;
      if(this.registerForm.invalid){
        if(this.contactservice.Form.get('$key').value==null)
        this.contactservice.insertcontact(this.contactservice.Form.value);
        this.submitted= true;
        this.shoeSuccessMeassage= true;
        setTimeout(()=> this.shoeSuccessMeassage= false,3000);
        
              
        
      }
      console.log('fullname='+this.f.fullname.value);
      console.log('email='+this.f.email.value);
      console.log('mobile='+this.f.mobile.value);
    }
  }
    
  
 
  
  



